'use client'

import { useState } from 'react'
import { updateHabit, deleteHabit, toggleHabitCompletion, type Habit } from '@/lib/api'
import HabitForm from './HabitForm'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Check, Edit, Trash2 } from 'lucide-react'

interface HabitItemProps {
    habit: Habit
    onUpdate: (habit: Habit) => void
    onDelete: (habitId: number) => void
}

export default function HabitItem({ habit, onUpdate, onDelete }: HabitItemProps) {
    const [isEditing, setIsEditing] = useState(false)
    const [isCompleted, setIsCompleted] = useState(false)
    const [loading, setLoading] = useState(false)

    const today = new Date().toISOString().split('T')[0]

    const handleUpdate = async (habitData: Omit<Habit, 'id' | 'createdAt' | 'updatedAt'>) => {
        try {
            const updatedHabit = await updateHabit(habit.id, habitData)
            onUpdate(updatedHabit)
            setIsEditing(false)
        } catch (err) {
            console.error('Error updating habit:', err)
        }
    }

    const handleDelete = async () => {
        if (confirm('Are you sure you want to delete this habit?')) {
            try {
                await deleteHabit(habit.id)
                onDelete(habit.id)
            } catch (err) {
                console.error('Error deleting habit:', err)
            }
        }
    }

    const handleToggleCompletion = async () => {
        try {
            setLoading(true)
            await toggleHabitCompletion(habit.id, today)
            setIsCompleted(!isCompleted)
        } catch (err) {
            console.error('Error toggling completion:', err)
        } finally {
            setLoading(false)
        }
    }

    if (isEditing) {
        return (
            <HabitForm
                habit={habit}
                onSubmit={handleUpdate}
                onCancel={() => setIsEditing(false)}
            />
        )
    }

    return (
        <Card className="relative">
            <div
                className="absolute top-0 left-0 w-1 h-full rounded-l"
                style={{ backgroundColor: habit.color }}
            />
            <CardHeader className="pb-3">
                <CardTitle className="text-lg">{habit.name}</CardTitle>
                {habit.description && (
                    <p className="text-sm text-gray-600">{habit.description}</p>
                )}
            </CardHeader>
            <CardContent>
                <div className="flex items-center justify-between">
                    <Button
                        onClick={handleToggleCompletion}
                        disabled={loading}
                        variant={isCompleted ? "default" : "outline"}
                        size="sm"
                        className="flex items-center space-x-2"
                    >
                        <Check className="w-4 h-4" />
                        <span>{isCompleted ? 'Completed' : 'Mark Done'}</span>
                    </Button>
                    <div className="flex space-x-2">
                        <Button
                            onClick={() => setIsEditing(true)}
                            variant="ghost"
                            size="sm"
                        >
                            <Edit className="w-4 h-4" />
                        </Button>
                        <Button
                            onClick={handleDelete}
                            variant="ghost"
                            size="sm"
                            className="text-red-600 hover:text-red-700"
                        >
                            <Trash2 className="w-4 h-4" />
                        </Button>
                    </div>
                </div>
            </CardContent>
        </Card>
    )
}